@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}


.App{width:100%;height:100vh;background-color:#fff;mix-blend-mode:normal;margin:0 auto;font-family:'Roboto', sans-serif}

.wrapper{width:100%;height:100%;margin:0 auto;display:grid;grid-template-columns:repeat(4, 16px) repeat(86, 1fr)}.main{grid-column:5/48;height:100%}.slider{grid-column:48/91;height:100%}

.sidebar{grid-column:1/5;height:100%;background-color:#151b1f;display:grid;grid-template-columns:repeat(4, 16px)}.sidebar__lang{color:#0EC261;grid-column:2/4;align-self:flex-end;justify-self:center;font-weight:bold;font-style:normal;font-size:13px;line-height:15px;text-shadow:0px 4px 4px rgba(0,0,0,0.25);width:23px;height:16px;margin-bottom:32px}.menu{grid-column:2/4}.menu__toggle{opacity:0;position:fixed;top:32px;left:16px;width:32px;height:32px;z-index:2;margin:0}.menu__btn{display:flex;align-items:center;position:fixed;top:32px;left:16px;width:32px;height:32px;z-index:1;justify-content:center}.menu__btn>span,.menu__btn>span::before,.menu__btn>span::after{display:block;position:absolute;width:24px;height:3px;background-color:#fff}.menu__btn>span::before{content:"";top:-8px}.menu__btn>span::after{content:"";top:8px}.menu__box{display:block;position:fixed;visibility:hidden;top:0;left:-100%;width:100%;height:100%;margin:0;padding:80px 0;list-style:none;text-align:left;background-color:#111518;opacity:.9;box-shadow:1px 0px 6px rgba(0,0,0,0.2)}.menu__item{display:block;padding:12px 24px;color:#fff;font-family:"Roboto", sans-serif;font-weight:500;font-size:32px;line-height:37.5px;text-decoration:none;margin-left:128px}.menu__box li:nth-child(1){margin-top:128px}.menu__item:hover{color:#0EC261}.menu__toggle:checked ~ .menu__btn>span{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.menu__toggle:checked ~ .menu__btn>span::before{top:0;-webkit-transform:rotate(0);transform:rotate(0)}.menu__toggle:checked ~ .menu__btn>span::after{top:0;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.menu__toggle:checked ~ .menu__box{visibility:visible;left:0}.menu__btn>span,.menu__btn>span::before,.menu__btn>span::after{-webkit-transition-duration:0.25s;transition-duration:0.25s}.menu__box{-webkit-transition-duration:0.25s;transition-duration:0.25s}.menu__item{-webkit-transition-duration:0.25s;transition-duration:0.25s}.menu__item__img_wrapper{display:flex}.menu__item__img_wrapper a{margin-top:38px}.menu__item__img_wrapper a:nth-child(2){margin-left:0;padding-left:0}.menu__item__img_wrapper a:nth-child(3){margin-left:0;padding-left:0}.menu__item__img_wrapper a:nth-child(1){margin-left:128px}.menu__item__img_wrapper a img:hover{-webkit-filter:brightness(0.5) sepia(1) saturate(2000%) hue-rotate(120deg);filter:brightness(0.5) sepia(1) saturate(2000%) hue-rotate(120deg)}.menu__item{display:flex}

.main{display:grid;grid-template-columns:1fr;grid-template-rows:repeat(25, 1fr)}.main__header{grid-row:1/4;display:grid;grid-template-columns:1fr 1fr}.main__slogan{font:700 normal 30px sans-serif;padding-top:32px;padding-left:64px;color:#0EC261;display:flex;align-items:flex-start;justify-content:flex-start}.main__sity{font:400 normal 14px sans-serif;color:#999;padding-top:39px;display:flex;align-items:flex-start;justify-content:flex-end;width:100%;height:18px}.main__sity_marker{margin-right:7.54px}.main__sity_name{display:flex;align-items:center;justify-content:center;margin-right:64px}.main__section{grid-row:4/24;padding:130px 64px 64px 64px;display:flex;flex-direction:column}.main__section_karshering{font:700 normal 70px sans-serif;color:#121212;line-height:65.63px}.main__section_slogan{font:bold normal 70px sans-serif;color:#0EC261;line-height:66px}.main__section_minute{font:300 normal 26px sans-serif;color:#999;line-height:30px;margin-top:34px}.main__section_btn{font:500 normal 18px sans-serif;color:#fff;line-height:21.09px;margin-top:60px;background:-webkit-gradient(linear, left top, right top, color-stop(2.61%, #0ec261), color-stop(112.6%, #039f67));background:linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);width:250px;height:48px;border-radius:8px;border:0}.main__footer{grid-row:24/26;display:grid;grid-template-columns:1fr 1fr}.main__years{font:400 normal 13px sans-serif;line-height:15px;color:#999;display:flex;align-items:flex-end;justify-content:flex-start;margin-bottom:32px;margin-left:64px}.main__phone{font:400 normal 13px sans-serif;line-height:15px;color:#121212;display:flex;align-items:flex-end;justify-content:flex-end;margin-right:64px;margin-bottom:32px}

